.modal{
    height: 100vh;
    width: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    background-color: red;
}

.modal.active{
    transform: scale(1);
    opacity: 1;
    pointer-events: all;
}

.modal__content{
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    height: 200px;
    width: 400px;
}